import Vue from 'vue'
import VueRouter from 'vue-router'
import Product from '../views/Product.vue'
import Edit from '../views/Edit.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Product
  },
  {
    path: '/edit/:form',
    name: 'edit',
    component: Edit
  }
  
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router

